import { Component } from '@angular/core';
import { ActionSheetController  ,AlertController, IonicPage,ModalController, NavController, NavParams, InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';


@IonicPage()
@Component({
  selector: 'page-cursosview-list',
  templateUrl: 'cursosview-list.html',
})
export class CursosviewListPage {

  public loading: boolean = true;
  items: any=[];
  items2: any=[];
  permisom: any=[];
    generated = '';
    public hasFilter: boolean = false;
   
  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';
  username: string = "";
  idempresa:any;
  idafiliado:any='na';
//private barcodeScanner:BarcodeScanner,
  constructor(public actionSheetCtrl: ActionSheetController,public alertCtrl: AlertController,public storage: Storage, public msj: NotificationProvider, public translateService: TranslateService
    ,public navCtrl: NavController,
    private _MODAL  		: ModalController, public navParams: NavParams,
     public regService: Auth
    //, private qrScanner: QRScanner
   
    ) {
      this.translateService.get('APPX').subscribe((value) => {
        this.idempresa=value
       
    })
    
  }

  
  public clearSearchg(): void {
    this.gSearchQuery = '';
  }

  showFilter()     
  {

    let self = this;
    self.loading = true;
   
  let actionSheet = this.actionSheetCtrl.create({
    title: 'Filtrar por estado:',
    buttons: [
      {
        text: 'Activos',
        handler: () => {
        
          this.items=this.items2
          this.items= this.items2.filter((v) => {
         
            if(v.estado ) {
              
              if (v.estado=='Activo') {
                return true;
              }
              return false;
              
            }
          });
          self.loading = false;
         
          this.hasFilter = true;
        }
      },
      {
        text: 'Inactivos',
        handler: () => {
          this.items=this.items2
          this.items= this.items2.filter((v) => {
            if(v.estado ) {
              self.loading = false;
              if (v.estado=='Inactivo') {
                return true;
              }
              return false;
              
            }
          });   
        }
      }
     
    ]
  });

  actionSheet.present();



  }

 

  
  OnExport = function ()
  {
    this.regService.abrelinkinterno('datosfijos/excel-plantillas');
    
  }



  pideregistros() { 
      let self = this;
      self.loading = true;
      this.regService.getReg(this.navParams.data.idempresa + '/todos','plantillas').then((data) => {
        this.items=data;
        this.items2=data;
        self.loading = false;
  }, (err) => {
    this.msj.showError3('Error',err.error);
   // this.translateService.get('NO_OPERACION').subscribe((value) => { this.msj.showError3('Error',value);  })  
  });
  }
  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 
  }
  ionViewDidLoad() {
    this.pideregistros();
  }
  
  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }


  deleteRecord(item : any, no) : void
  {

    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operación?',
    buttons: [
      {text: 'Si',
        handler: () => {
              
          let recordID 		: string		= item._id;
          this.regService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'plantillas').then((data) => {
              this.items.splice(no, 1);
              this.items = this.items.concat([]);
              this.items2=this.items
            }, (err) => {
              this.msj.showError3('Error',err.error);
          });
      
        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();


    
  
  }
  value:any;

  updateRecord(item : any,no) : void
   {
    let addModal = this._MODAL .create('PlantillaCreatePage', { record : item,idempresa:this.navParams.data.idempresa },{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.pideregistros() 
      }
    })
    addModal.present();
    //  this.navCtrl.push('EmpresaCreatePage', { record : item });
   }
   addRecord() : void
   {
    let addModal = this._MODAL .create('PlantillaCreatePage',{ record : null ,idempresa:this.navParams.data.idempresa},{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.pideregistros() 
      }  
    })
    addModal.present();

      //this.navCtrl.push('EmpresaCreatePage');
   }
   participaRecord(item : any) : void
   {
   
      this.navCtrl.push('PlantilladListPage', { record : item,record2:{idempresa:this.navParams.data.idempresa,idplantilla:item._id,nombrep:item.nombre} });
   }
   conferenciaRecord(item : any) : void
   {
   
      this.navCtrl.push('PlantilladmListPage', { record : item,record2:{idempresa:this.navParams.data.idempresa,idplantilla:item._id,nombrep:item.nombre} });
   }

   viewRecord(item : any): void
   {
     // let modal = this._MODAL.create(ViewGalleryPage, { record : item });
     // modal.present();
   }


   
  tomaqrpage(item : any) : void
  {
   let addModal = this._MODAL .create('CodebarpagPage', { record : item },{showBackdrop: true,enableBackdropDismiss: false});
   
   addModal.present();
   //  this.navCtrl.push('EmpresaCreatePage', { record : item });
  }
 

  verinfo(item,op){
    this.regService.getReg(item._id + '/'+ item.idempresa + '/' + op,'plantillads').then((data) => {
 
    if(data[0].descripcion)
    {
      this.navCtrl.push('CursosviewHtmlPage', { html: data[0].descripcion,record : item,titulo:op});

    }
    else{
      this.msj.showError3('Información','No existe información');
    }
      

     
  }, (err) => {
    this.msj.showError3('Error',err.error);
  });


    


  }

  iniciarcurso(item){

    this.navCtrl.push('CursosviewUnidadesPage', { record : item,idplantilla:item._id,idempresa:item.idempresa});

  }

}



